import React, { useEffect } from "react";
import { useGlobalContext } from "../../context";

const Polyplast = () => {
  const { setPath } = useGlobalContext();

  useEffect(() => {
    setPath("business");
    // eslint-disable-next-line
  }, []);

  return (
    <div className="Parent">
      <div className="Container">
        <h1>Polyplast</h1>
      </div>
    </div>
  );
};

export default Polyplast;
